@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jaldi:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App * {
  box-sizing: border-box;
}

.App {
  text-align: center;
  padding: 1% 5%;
  background-color: #fef7ec;
  font-family: "Jaldi", sans-serif;
  font-size: 16px;
}

h1 {
  margin: 10px auto;
  color: #32161f;
  font-weight: 700;
}

h2 {
  font-weight: 700;
}

h3 {
  margin: 5px auto;
  font-family: "Caveat", cursive;
}

span {
  font-weight: 700;
}

ul {
  padding: 0;
  margin: 20px auto;
  list-style: none;
}

p {
  margin: 0;
}

button.eventCard--link {
  background-color: #e7dada;
  padding: 5px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  border-color: transparent;
  box-shadow: 5px 10px 18px -9px #343746;
  margin: 10px;
}

button.eventCard--link a {
  text-decoration: none;
  color: #000;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

input[type="number"] {
  margin: 5px;
}

.city {
  width: 250px;
}

.eventNumInput {
  margin: 0 auto;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: 15px auto;
  position: relative;
}

.NumberOfEvents {
  width: 250px;
  margin: 20px auto;
  text-align: center;
}

label.eventNumLabel,
label.CitySearch-label {
  padding: 10px;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 85px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:hover {
  background-color: #be9d9d;
}

.Alert {
  position: fixed;
  top: 110px;
  right: 95px;
  font-weight: bold;
  background-color: #fff;
  font-size: 1.5rem;
  z-index: 100;
  animation: 2s shakeFx ease-in-out infinite;
}

.event {
  padding: 20px;
  margin-bottom: 30px;
  text-align: left;
  border: 1px solid #6e4949;
  border-radius: 4px;
  position: relative;
}

.details-btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 5px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: #dcdde5;
  border-color: transparent;
  box-shadow: 5px 10px 18px -9px #343746;
}

.description {
  overflow-x: hidden;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: row;
}

div#undefined.recharts-responsive-container {
  width: 100%;
}

/* Animations */

@keyframes shakeFx {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0);
  }
}

@media screen and (max-width: 767px) {
  .Alert {
    top: 5px;
    left: 0;
    font-size: 1rem;
    padding: 10px;
  }
  h1 {
    margin: 15px 5px auto;
  }

  h3 {
    margin: 3px auto;
  }

  .data-vis-wrapper {
    display: flex;
    align-items: center;
    flex-flow: column wrap;
  }

  div#undefined.recharts-responsive-container {
    width: 100%;
  }

  .recharts-surface {
    overflow: visible;
  }

  .details-btn {
    position: absolute;
    right: 10px;
    bottom: 25px;
    padding: 5px;
  }

  h1 {
    margin: 0 auto;
  }

  .event {
    padding: 20px;
    padding-bottom: 80px;
  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1;
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
@keyframes nprogress-spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

